/**
 *
 */
export function validateIsEmbed() {
    window.console.log('validateIsEmbed');
    let isembedval = document.querySelector('[name="isembed"]');
    let embedSelector = document.getElementById('embed_selector');
    if(embedSelector.checked) {
        isembedval.value = "1";
    } else {
        isembedval.value = "0";
    }
}

/**
 * @param {string} id
 * @param {string} parentContainer
 * @param {string} nextElement
 */
export function validateEmbed(id, parentContainer, nextElement) {
    let embedInput = document.getElementById(id);
    let input = embedInput.value;
    if (input === '' || input === null) {
        return true;
    }
    let isValid = true;

    if (input.slice(0, 7) !== '<iframe') {
        isValid = false;
    }
    if (input.slice(-9) !== '</iframe>' && input.slice(-2) !== '/>') {
        isValid = false;
    }

    if(!isValid) {
        let errorMsg = document.getElementById('error_msg_introduction_embed');
        if (errorMsg === null) {
            let span = document.createElement('span');
            span.style.color = 'red';
            span.id = 'error_msg_introduction_embed';
            span.style.margin = '0 0 0.5rem 1rem';
            span.innerHTML =
                '* L\'url du fichier embed doit Ãªtre au format: \'&lt;iframe&gt;&lt;/iframe&gt;\' ou \'&lt;iframe/&gt;\'';
            document.getElementById(parentContainer).insertBefore(
                span, document.getElementById(nextElement));
        }
    }
    return isValid;
}